.awards-content {
    max-width: 800px;
    margin: 0 auto 20px auto;
    padding: 15px;
    border: 1px solid #dcdcdc;
    background-color: white;
    text-align:left;

    h3 {
        display: block;
        font-weight: 400;
        margin: auto;
        padding-left: 15px;
        padding-bottom: 10px;
        border-bottom: 1px solid #dcdcdc;
    }

    dd {
        margin: 0;
        padding: 0 0 0 10px;
        color: #000000;
        font-size: 0.9rem;
        &:before  {
            transform: translateX(-8px);
            position: absolute;
            content: '· '
        }
    }
}