.education-content {
    max-width: 800px;
    margin: 0 auto 20px auto;
    padding: 15px;
    border: 1px solid #dcdcdc;
    background-color: white;
    text-align:left;

    h3 {
        font-weight: 500;
        float: left;
        margin: 0;
    }

    h4 {
        color: #74808a;
        font-weight: 500;
        clear: both;
        margin: 0 0 10px 0;
    }

    span {
        color: #74808a;
        float: right;
    }

    dd {
        margin: 0;
        padding: 0 0 0 10px;
        color: #000000;
        font-size: 0.9rem;
        &:before  {
            transform: translateX(-8px);
            position: absolute;
            content: '· '
        }
    }
}