.project {
    display: flex;
    margin: 0 auto 40px auto;
    max-width: 900px;
    background-color: white;
    border-radius: 4px;
    box-shadow: 0 3px 6px rgba(0,0,0,0.08), 0 3px 6px rgba(0,0,0,0.15);
    text-align: left;

    &-image {
        flex-shrink: 0;
        border-radius: 4px 0 0 4px;
        border-right: 1px solid rgba(0,0,0,0.08)
    }
    &-description {
        padding: 10px;
    }

    h3 {
        margin-top: 20px;
        margin-bottom: 5px;
        font-size: 1.5rem;
        font-weight: 300;
        color: #374054;
    }

    h4 {
        margin-top: 5px;
        margin-bottom: 20px; 
        font-size: 1rem;
        font-weight: normal;
        color: #74808a;
    }
}

@media only screen and ( max-width: 850px ) {
    .project {
        display: block;
        width: 384px;
        
        &-image {
            border-radius: 4px 4px 0 0;
            border-bottom: 1px solid rgba(0,0,0,0.08)
        }
    }
}

@media only screen and ( max-width: 425px ) {
    .project {
        width: auto;

        &-image {
            width: 100%;
        }
    }
}