.skillsBox {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1000px;
}

.skill {
    display: inline-block;
    margin: 30px;
    padding: 15px;
    background-color: white;
    border-radius: 4px;
    box-shadow: 0 2px 6px rgba( 0, 0, 0, 0.1 );
}

@media only screen and ( max-width: 800px ) {
    .skill {
        margin: 20px;
    }
}