.headerBar-big {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    text-align: center;

    animation-name: _dropDown;
    animation-duration: 1s;
    @keyframes _dropDown {
        0% { transform: translateY(-100%) }
        100% { transform: translateY(0) }
    }
}

.headerBar-small {
    position: fixed;
    top: 5vw;
    right: 5vw;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.Header-items { 
    display: inline-block;
    padding: 12px;
    margin: 0px;
    list-style: none;
    text-align: center;
    background-color: rgba(0,0,0,0.8);
    border-bottom-right-radius: 4px;
    border-bottom-left-radius: 4px;
}

.Header-item { 
    padding: 10px 17px 10px 17px;
    color: white;
    border-radius: 4px;

    &:hover {
        cursor: pointer;
        background-color: #444444;
        transition-duration: 0.5s;
    }

    &-inline-block {
        @extend .Header-item;
        display: inline-block;
        padding: 5px 14px 5px 14px;
    }
}

.button {
    &-based-style {
        width: 35px;
        height: 5px;
        background-color: #444444;
        margin: 6px;
        transition: 0.5s;
    }
    &-status-close {
        transition-duration: 0.5s;
        .buttonBar1, .buttonBar2, .buttonBar3 {
            @extend .button-based-style;
        }
    }
    &-status-open {
        margin-left: 40.406px;
        margin-right: 40.406px;
        transition-duration: 0.5s;
        .buttonBar1 {
            @extend .button-based-style;
            transform: rotate(-45deg) translate(-9px, 6px);
        }
        .buttonBar2 {
            @extend .button-based-style;
            opacity: 0;
        }
        .buttonBar3 {
            @extend .button-based-style;
            transform: rotate(45deg) translate(-9px, -6px);
        }
    }
}

