.about-content {
    display: flex;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 50px;
    padding-left: 60px;
    padding-right: 60px;
    max-width: 900px;

    .profile {
        margin-right: 70px;
        flex-shrink: 0;
    }
}

@media only screen and ( max-width: 900px ) {
    .about-content {
        display: block;
        padding-left: 30px;
        padding-right: 30px;
        .profile {
            margin-right: auto;

            &-image {
                width: 123.9px;
                height:165.2px;
            }
        }
    }
}